@import "variables.scss";

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-width: 375px;
  overflow-x: hidden;
}

h2,
h3,
p {
  margin: 0;
}

ul,
img {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

@font-face {
  font-family: "Inter-Bold";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
}

@font-face {
  font-family: "Inter-SemiBold";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Inter-Regular";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}


.wrap {
  width: 375px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
